body {
    margin: 0px;
    padding: 0px 0px 0px 0px;
    background-color: #4d4b4b;
    font-family: sans-serif;
}

h1 {
    margin: 0px 0px 0px 0px;
    color: aquamarine;
}

.App {
    overflow: hidden;
}

header {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    padding: 20px;
    background-color: #252525;
    z-index: 2;
}

.__container {
    max-width: 1000px;
    margin: 0px auto;
}

.header__title {
    font-size: 18px;
    color: #fff;
}

nav {
    margin: 70px 0px 0px 0px;
}

nav img {
    position: relative;
    left: 50%;
    top: 0;
    transform: translate(-50%, 0%);
}


.button__row.__container {
    display: flex;
    justify-content: space-between;
}

.button__row button {
    width: 100px;
    height: 50px;
    border-radius: 10px;
    padding: 0;
    border: 0;
    background-color: #406651;
    color: #fff;
    font-size: 22px;
    transition: background-color 0.6s ease 0s;
}

button:hover {
    background-color: #058840;
}

.button__row p {
    font-size: 18px;
    background-color: #fff;
    padding: 5px;
    border-radius: 10px;
}

@media (max-width: 500px) {
    nav img {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }

    nav {
        width: 100%;
        height: 100%;
    }

    .n-image {
        padding: 0px 10px;
    }

    .button__row button {
        width: 80px;
        height: 30px;
        font-size: 18px;
    }

    .button__block {
        padding: 0px 10px;
    }

    .button__row p {
        font-size: 16px;
        background-color: #fff;
        padding: 5px;
        border-radius: 8px;
        font-weight: 600;
    }
}

@media (prefers-color-scheme: light) {
    body {
        color: #213547;
        background-color: #b6b6b6;
    }

    a:hover {
        color: #747bff;
    }

    button {
        background-color: #f9f9f9;
        color: #333;
    }

    header {
        background-color: #d8d8d8;
    }

    .header__title {
        color: #333;
    }

    .button__row p {
        background-color: #3a3a3a;
        color: #ffffff;
    }
}